    .ContentModal_landscape {
        object-fit: contain;
        border-radius: 10px;
        -webkit-border-radius: 10px;
        -moz-border-radius: 10px;
        -ms-border-radius: 10px;
        -o-border-radius: 10px;
    }
    
    .ContentModal_portrait {
        display: none;
        object-fit: contain;
        border-radius: 10px;
        -webkit-border-radius: 10px;
        -moz-border-radius: 10px;
        -ms-border-radius: 10px;
        -o-border-radius: 10px;
    }
    
    .tagline {
        padding-bottom: 10px;
        align-self: center;
    }
    
    .ContentModal {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        height: 100%;
        width: 100%;
        overflow-y: scroll;
        scrollbar-width: none;
    }
    
    .ContentModal::-webkit-scrollbar {
        display: none;
    }
    
    .ContentModal_about {
        padding: 10px;
        width: 95%;
        height: 90%;
        display: flex;
        flex-direction: column;
        font-weight: 300;
    }
    
    .ContentModal_title {
        /*height: 12%;*/
        font-size: 5vw;
        display: flex;
        align-items: center;
        justify-content: center;
    }
    
    .ContentModal_description {
        display: flex;
        height: 40%;
        /* overflow-y: scroll; */
        padding: 15px;
        border-radius: 20px;
        -webkit-border-radius: 20px;
        -moz-border-radius: 20px;
        -ms-border-radius: 20px;
        -o-border-radius: 20px;
        scrollbar-width: thin;
        box-shadow: inset 0 0 5px #000000;
        text-align: justify;
    }
    
    .ContentModal_description::-webkit-scrollbar {
        display: none;
    }
    
    @media(min-width:835px) {
        .ContentModal_landscape {
            display: none;
        }
        .ContentModal_portrait {
            display: flex;
            width: 38%;
        }
        .ContentModal {
            flex-direction: row;
            justify-content: space-around;
            padding: 10px 0;
        }
        .ContentModal_about {
            width: 58%;
            padding: 0;
            height: 100%;
            justify-content: space-evenly;
        }
        .ContentModal_title {
            font-size: 3.5vw;
        }
        .ContentModal_description {
            font-size: 22px;
        }
    }