* {
    scroll-behavior: smooth;
}

.app {
    min-height: 100vh;
    background-color: #39445a;
    color: white;
    padding-top: 30px;
    padding-bottom: 70px;
}

.pageTitle {
    text-transform: uppercase;
    display: flex;
    justify-content: center;
    font-family: 'Montserrat', sans-serif;
    font-size: 3.5rem;
    padding: 4px;
    border-radius: 50px;
    -webkit-border-radius: 50px;
    -moz-border-radius: 50px;
    -ms-border-radius: 50px;
    -o-border-radius: 50px;
    color: white;
}

.css-heg063-MuiTabs-flexContainer {
    width: 100%;
}

.css-1fvudcs-MuiInputBase-root-MuiOutlinedInput-root{
    border-radius: 90px !important;
    -webkit-border-radius: 90px !important;
    -moz-border-radius: 90px !important;
    -ms-border-radius: 90px !important;
    -o-border-radius: 90px !important;
}


.css-19micn4-MuiButtonBase-root-MuiPaginationItem-root{
    color:white !important;
}

.css-1n7mzq0-MuiInputBase-root-MuiFilledInput-root{
    border-radius:90px !important;
    -webkit-border-radius:90px !important;
    -moz-border-radius:90px !important;
    -ms-border-radius:90px !important;
    -o-border-radius:90px !important;
    border-bottom:none !important
}

.media:hover, .homefilm:hover{
    box-shadow: 0px 30px 18px -8px rgba(0, 0, 0, 0.1);
    transform: scale(1.05, 1.05);
    opacity: 0.5;
}

@media(max-width:1000px) {
    .pageTitle {
        font-size: 6.4vw;
    }
    .app{
        padding-top:10px;
    }
}

